/*****************************
  Helper Classes
*****************************/

/* Color */
.text-primary {
  color: $primary !important;
}

.text-white {
  color: $white;
}

.text-gray {
  color: $gray-4 !important;
}

.text-dark {
  color: $gray-9 !important;
}

.text-light {
  color: $gray-3 !important;
}

.text-light-gray {
  color: $gray-2 !important;
}

.text-muted {
  color: $gray-1 !important;
}

.text-body {
  color: $gray-3 !important;
}

/* Letter Spacing */
.letter-spacing {
  letter-spacing: 1px;
}

/* Background */
.bg-primary {
  background-color: $primary !important;
}

.bg-dark {
  background-color: $gray-9 !important;
}

.bg-dark-1 {
  background-color: $gray-6 !important;
}

.bg-gray {
  background-color: $gray-4 !important;
}

.bg-light {
  background-color: $gray-1 !important;
}

.bg-black {
  background-color: $black !important;
}

/* Border */
.border-primary {
  border-color: $primary !important;
}

.border-dark {
  border-color: $gray-9 !important;
}

.border-gray {
  border-color: $gray-4 !important;
}

.border-white {
  border-color: $white !important;
}

/* Page section padding */
.space-ptb {
  padding: 110px 0;
}

.space-pt {
  padding-top: 110px;
}

.space-pb {
  padding-bottom: 110px;
}

.space-sm-ptb {
  padding: 60px 0;
}

.space-sm-pt {
  padding-top: 60px;
}

.space-sm-pb {
  padding-bottom: 60px;
}

.mt-n6 {
  margin-top: -5rem !important;
}

.mt-n7 {
  margin-top: -7rem !important;
}

.h-500 {
  height: 500px;
}

/* font size */
.font-sm {
  font-size: 13px !important;
}

.font-md {
  font-size: 15px !important;
}

.font-lg {
  font-size: 17px !important;
}

.line-height-1{
  line-height: 1;
}
.line-height-2{
  line-height: 2;
}

/* box shadow */
.shadow {
  box-shadow: $box-shadow !important;
}

/* Img Holder */
.bg-holder {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  width: 100%;
}

/* Background overlay */
.bg-overlay-black-10 {
  position: relative;
  z-index: 1;

  &:before {
    background: rgba($black, 0.1);
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 0;
  }

}

