/* Avatar */
.avatar {
  min-width: 50px;
  width: 50px;
  display: inline-block;
}

.avatar.avatar-sm {
  min-width: 20px;
  width: 20px;
}

.avatar.avatar-md {
  min-width: 60px;
  width: 60px;
}

.avatar.avatar-lg {
  min-width: 90px;
  width: 90px;
}

/* Border width */
.border {
  border-color: $border-color !important;
}

.border-top {
  border-color: $border-color !important;
}

.border-1 {
  border-width: 1px !important;
}

.border-2 {
  border-width: 2px !important;
}

.border-3 {
  border-width: 3px !important;
}

.border-4 {
  border-width: 4px !important;
}

.border-5 {
  border-width: 5px !important;
}

/* Z index */
.z-index-1 {
  z-index: 1;
}

.z-index-9 {
  z-index: 9;
}

.z-index-99 {
  z-index: 99;
}

.z-index-999 {
  z-index: 999;
}

/* Opacity */
.opacity-1 {
  opacity: 0.1;
}

.opacity-2 {
  opacity: 0.2;
}

.opacity-3 {
  opacity: 0.3;
}

.opacity-4 {
  opacity: 0.4;
}

.opacity-5 {
  opacity: 0.5;
}

.opacity-6 {
  opacity: 0.6;
}

.opacity-7 {
  opacity: 0.7;
}

.opacity-8 {
  opacity: 0.8;
}

.opacity-9 {
  opacity: 0.9;
}

/*****************************
  Responsive
*****************************/
@media (max-width:1199px) {

  /* Page Section Padding */
  .space-ptb {
    padding: 70px 0;
  }

  .space-pt {
    padding-top: 70px;
  }

  .space-pb {
    padding-bottom: 70px;
  }

}

@media (max-width:991px) {

  /* Page Section Padding */
  .space-ptb {
    padding: 60px 0;
  }

  .space-pt {
    padding-top: 60px;
  }

  .space-pb {
    padding-bottom: 60px;
  }

  /* Half Overlay */
  .half-overlay:before {
    content: none;
  }

  /* Border */
  .border-lg-none {
    border: none !important;
  }

}

@media (max-width:767px) {

  /* Page Section Padding */
  .space-ptb {
    padding: 50px 0;
  }

  .space-pt {
    padding-top: 50px;
  }

  .space-pb {
    padding-bottom: 50px;
  }

  .space-sm-pt {
    padding-top: 50px;
  }

  .space-sm-pb {
    padding-bottom: 50px;
  }

  .space-sm-ptb {
    padding: 50px 0;
  }

  .container {
    max-width: 100%;
  }

  /* Border */
  .border-md-none {
    border: none !important;
  }

  .mt-n6 {
   margin-top: -3rem !important;
  }

 .mt-n7 {
   margin-top: -5rem !important;
 }

}

@media (max-width:575px) {

  /* Page Section Padding */
  .space-ptb {
    padding: 40px 0;
  }

  .space-pt {
    padding-top: 40px;
  }

  .space-pb {
    padding-bottom: 40px;
  }

  .space-sm-pt {
    padding-top: 40px;
  }

  .space-sm-pb {
    padding-bottom: 40px;
  }

  .space-sm-ptb {
    padding: 40px 0;
  }

  /* Border */
  .border-sm-none {
    border: none !important;
  }

 .mt-n6 {
   margin-top: -2rem !important;
  }

 .mt-n7 {
   margin-top: -3rem !important;
 }

}
