  height: 60px;
  text-align: center;
  background: #ffffff;
  border-radius: 50%;
  align-items: center;
  align-self: center;
  justify-content: center;
  color: #000000; }

.icon-btn {
  opacity: 1; }

.icon-btn.btn-animation {
  opacity: 1; }

.icon-btn-all-center {
  position: absolute;
  top: 50%;
  left: 50%;
